.contact {
  &-title {
    color: $nav-contact;
  }

  &-info {
    @include card-top;
    padding: 20px;
    line-height: 1.5;
    font-size: 14px;

    &-city {
      margin-top: 10px;
    }
  }

  &-map {
    @include card-bottom-no-pad;
    @include shadow;
    border-radius: 0 0 3px 3px;
    margin: auto;
    margin-bottom: 25px;
    min-width: 100%;
  }

  @media only screen and (min-width: $responsive-width) {
    &-map {
      // height: 360px;
      min-width: 426px;
    }
  }
}



