.side-bar {
  @include card;
  padding: 40px 20px;
  text-align: center;

  &-headshot {
    &-image {
      border-radius: 160px;
    }
  }

  &-content {
    width: 218px;
    margin: 0 auto;
    padding: 10px;

    &-name {
      margin: 10px 0;
      padding-bottom: 10px;
    }

    &-social {
      @include shadow-interactive;
      margin: 0 6px;

      &:hover {
        transform: scale(1.05);
      }

      &-git {
        @include shadow-interactive;
        border-radius: 50%;
        margin: 0 6px;

        &:hover {
          transform: scale(1.05);
        }
      }
    }
  }

  @media only screen and (min-width: $responsive-width) {
    position: fixed;
    padding: 20px
  }
}



