@font-face {
  font-family: 'Peter Mono';
  src: url("../public/fonts/peter_mono_comfortable.otf") format("opentype");
}

.main-wrapper {
  margin: 0 auto;
  width: 100%;
}

.main-content,
.left-column,
.right-column {
  margin: 0 auto;
}

.left-column {
  padding: 25px;
}

@media only screen and (min-width: 900px) {
  .main-wrapper {
    width: 900px;
  }

  .left-column {
    float: left;
    width: 600px;
  }

  .right-column {
    float: right;
    margin-top: 40px;
    text-align: center;
    width: 250px;
  }
}

.hidden {
  display: none;
}

.not-linky {
  color: #505962;
}

.not-linky:hover {
  color: #505962;
}

.not-linky:active {
  color: #505962;
}

body {
  background: #f6f6f6;
  color: #505962;
  font-family: 'Dosis', sans-serif;
  font-size: 14px;
  font-weight: 400;
  margin: 0;
  min-width: 454px;
  padding: 0;
}

a {
  color: #4788b7;
  text-decoration: none;
}

a:hover {
  color: #4c9c7b;
}

div {
  margin: 0;
  padding: 0;
}

h1 {
  color: #1a1a1a;
  font-size: 32px;
  font-weight: 600;
  margin-bottom: 40px;
}

h2 {
  color: #1a1a1a;
  font-size: 32px;
  font-weight: 700;
  margin-bottom: 40px;
  margin-top: 60px;
}

h3 {
  color: #1a1a1a;
  font-size: 19px;
  margin: 0;
}

img {
  border-radius: 4px;
}

p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0 0 0 17px;
  padding: 0 0 0 20px;
}

header {
  width: 100%;
  margin: 0;
  padding: 0;
}

nav {
  font-family: 'Dosis', sans-serif;
  font-size: 20px;
  text-align: center;
  font-weight: 300;
  letter-spacing: 1px;
  width: 100%;
  padding: 10px 20px 10px 0;
  position: fixed;
  z-index: 10;
  box-shadow: 0 3px 2px rgba(0, 0, 0, 0.1);
  transition: background 250ms ease-out;
}

.nav-about {
  background: #4788b7;
}

.nav-experience {
  background: #4c9c7b;
}

.nav-portfolio {
  background: #5e5e65;
}

.nav-contact {
  background: #4c9cc1;
}

.nav-underlay {
  height: 36px;
}

.nav-link {
  margin: 0 20px 0 20px;
  font-weight: 400;
  color: #fff;
  text-decoration: none;
}

.nav-link-active {
  margin: 0 20px 0 20px;
  font-weight: 400;
  text-decoration: none;
  color: #f4e6c2;
  cursor: default;
}

.nav-link-active:hover {
  color: #f4e6c2;
}

.nav-link:hover {
  color: #f4e6c2;
}

@media only screen and (min-width: 900px) {
  .nav-link,
  .nav-link-active {
    font-size: 16px;
  }
}

.side-bar {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  transition: box-shadow .3s ease-in-out, transform .2s ease-in-out;
  background: #fff;
  border-radius: 3px;
  padding: 20px;
  padding: 40px 20px;
  text-align: center;
}

.side-bar-headshot-image {
  border-radius: 160px;
}

.side-bar-content {
  width: 218px;
  margin: 0 auto;
  padding: 10px;
}

.side-bar-content-name {
  margin: 10px 0;
  padding-bottom: 10px;
}

.side-bar-content-social {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  transition: box-shadow .3s ease-in-out, transform .2s ease-in-out;
  cursor: pointer;
  margin: 0 6px;
}

.side-bar-content-social:hover {
  box-shadow: 0 6px 6px rgba(0, 0, 0, 0.08);
  transform: scale(1.01);
}

.side-bar-content-social:active {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  transform: scale(1.005);
}

.side-bar-content-social:hover {
  transform: scale(1.05);
}

.side-bar-content-social-git {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  transition: box-shadow .3s ease-in-out, transform .2s ease-in-out;
  cursor: pointer;
  border-radius: 50%;
  margin: 0 6px;
}

.side-bar-content-social-git:hover {
  box-shadow: 0 6px 6px rgba(0, 0, 0, 0.08);
  transform: scale(1.01);
}

.side-bar-content-social-git:active {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  transform: scale(1.005);
}

.side-bar-content-social-git:hover {
  transform: scale(1.05);
}

@media only screen and (min-width: 900px) {
  .side-bar {
    position: fixed;
    padding: 20px;
  }
}

.about-title {
  color: #4788b7;
}

.about-elevator-pitch {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  transition: box-shadow .3s ease-in-out, transform .2s ease-in-out;
  background: #fff;
  border-radius: 3px;
  padding: 20px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  transition: box-shadow .3s ease-in-out, transform .2s ease-in-out;
  cursor: pointer;
  clear: both;
  margin-bottom: 25px;
}

.about-elevator-pitch:hover {
  box-shadow: 0 6px 6px rgba(0, 0, 0, 0.08);
  transform: scale(1.01);
}

.about-elevator-pitch:active {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  transform: scale(1.005);
}

.about-elevator-pitch-title {
  cursor: pointer;
}

.about-elevator-pitch-paragraph {
  padding-top: 8px;
  margin-bottom: 0;
}

.about-code-list {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  transition: box-shadow .3s ease-in-out, transform .2s ease-in-out;
  background: #fff;
  border-radius: 3px;
  padding: 20px;
  margin-top: 10px;
  margin-bottom: 25px;
  list-style: none;
}

.about-code-list-title {
  margin-bottom: 10px;
}

.about-code-list-item {
  display: flex;
  align-items: center;
  font-family: 'Peter Mono', 'Dosis', 'monospace';
}

.about-code-list-item-name {
  margin-right: 6px;
}

.about-code-list-item-icon {
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 4px 8px 4px 20px;
}

.about-code-list-item-icon-image {
  max-width: 100%;
  max-height: 100%;
  border-radius: 0;
}

.contact-title {
  color: #4c9cc1;
}

.contact-info {
  background: #fff;
  border-radius: 3px 3px 0 0;
  margin-bottom: 0;
  padding: 20px;
  line-height: 1.5;
  font-size: 14px;
}

.contact-info-city {
  margin-top: 10px;
}

.contact-map {
  background: #fff;
  border-radius: 0 0 3px 3px;
  margin-top: 0;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  transition: box-shadow .3s ease-in-out, transform .2s ease-in-out;
  border-radius: 0 0 3px 3px;
  margin: auto;
  margin-bottom: 25px;
  min-width: 100%;
}

@media only screen and (min-width: 900px) {
  .contact-map {
    min-width: 426px;
  }
}

.experience-title {
  color: #4c9c7b;
}

.experience-pdf-tip {
  font-family: 'Peter Mono', 'Dosis', 'monospace';
  font-size: 13px;
  text-decoration: none;
  color: #999fac;
  display: inline-block;
  height: 100%;
  vertical-align: super;
  margin-left: 6px;
}

.experience-item-interactive {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  transition: box-shadow .3s ease-in-out, transform .2s ease-in-out;
  cursor: pointer;
}

.experience-item-interactive:hover {
  box-shadow: 0 6px 6px rgba(0, 0, 0, 0.08);
  transform: scale(1.01);
}

.experience-item-interactive:active {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  transform: scale(1.005);
}

.experience-item,
.experience-item-interactive {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  transition: box-shadow .3s ease-in-out, transform .2s ease-in-out;
  background: #fff;
  border-radius: 3px;
  padding: 20px;
  overflow: auto;
  margin-bottom: 25px;
}

.experience-item-content,
.experience-item-interactive-content {
  float: left;
  width: 100%;
}

.experience-item-left,
.experience-item-interactive-left {
  display: inline-block;
  padding-right: 20px;
  vertical-align: top;
  width: 50px;
}

.experience-item-right,
.experience-item-interactive-right {
  display: inline-block;
  width: calc(100% - 70px);
}

.experience-item-icon,
.experience-item-interactive-icon {
  width: 50px;
}

.experience-item-title,
.experience-item-interactive-title {
  margin-bottom: 10px;
}

.experience-item-title-no-pointer,
.experience-item-interactive-title-no-pointer {
  margin-bottom: 10px;
}

.experience-item-date,
.experience-item-interactive-date {
  font-family: 'Peter Mono', 'Dosis', 'monospace';
  color: #999fac;
  font-size: 14px;
}

.experience-item-dot,
.experience-item-interactive-dot {
  font-size: 19px;
  font-weight: 700;
  margin: 0 6px;
}

.experience-item-org,
.experience-item-interactive-org {
  margin-left: 0;
}

.experience-item-bullets,
.experience-item-interactive-bullets {
  padding-left: 0;
  padding-top: 16px;
  white-space: normal;
}

.portfolio-title {
  color: #5e5e65;
}

.portfolio-list:after {
  content: "";
  display: table;
  clear: both;
}

.portfolio-column {
  float: left;
  margin: 0;
  max-width: 300px;
  padding: 0;
}

.portfolio-column:after {
  clear: both;
  content: '';
  display: table;
}

.portfolio-item {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  transition: box-shadow .3s ease-in-out, transform .2s ease-in-out;
  cursor: pointer;
  float: left;
  margin: 0 10px 20px;
  width: 280px;
}

.portfolio-item:hover {
  box-shadow: 0 6px 6px rgba(0, 0, 0, 0.08);
  transform: scale(1.01);
}

.portfolio-item:active {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  transform: scale(1.005);
}

.portfolio-item-icon {
  background: #fff;
  border-radius: 3px 3px 0 0;
  margin-bottom: 0;
}

.portfolio-item-icon-image {
  background: #fff;
  border-radius: 3px 3px 0 0;
  margin-bottom: 0;
  display: block;
  margin: auto;
  width: 100%;
}

.portfolio-item-icon-image-small {
  border-radius: 23px;
  padding: 20px;
}

.portfolio-item-content {
  background: #fff;
  border-radius: 0 0 3px 3px;
  margin-top: 0;
  padding: 20px;
}

.portfolio-item-title {
  cursor: pointer;
}

.portfolio-item-dot {
  margin: 0 6px;
}

.portfolio-item-org {
  margin-left: 6px;
}

.portfolio-item-link {
  font-size: 16px;
}

.portfolio-item-description {
  padding-top: 25px;
}

.portfolio-item-date {
  color: #999fac;
  font-family: 'Peter Mono', 'Dosis', 'monospace';
  font-size: 14px;
}

.portfolio-item-keywords {
  color: #999fac;
  font-family: 'Peter Mono', 'Dosis', 'monospace';
  font-size: 13px;
  text-decoration: none;
}

.portfolio-item-bullets {
  margin-top: 20px;
}

@media only screen and (max-width: 900px) {
  .portfolio-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .portfolio-column {
    align-items: center;
    display: flex;
    flex-direction: column;
    float: none;
    max-width: 100%;
  }

  .portfolio-item {
    float: none;
    margin-bottom: 25px;
  }
}

.poster {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  transition: box-shadow .3s ease-in-out, transform .2s ease-in-out;
  cursor: pointer;
  width: 100%;
  border-radius: 4px;
  margin-top: 40px;
  cursor: pointer;
}

.poster:hover {
  box-shadow: 0 6px 6px rgba(0, 0, 0, 0.08);
  transform: scale(1.01);
}

.poster:active {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  transform: scale(1.005);
}

.poster-modal {
  width: 100%;
  padding: 0;
  border-radius: 4px;
}




