header {
  width: 100%;
  margin: 0;
  padding: 0;
}

nav {
  font-family: 'Dosis', sans-serif;
  font-size:20px;
  text-align:center;
  font-weight:300;
  letter-spacing: 1px;
  width:100%;
  padding:10px 20px 10px 0;
  position: fixed;
  z-index: 10;
  box-shadow: 0 3px 2px rgba(0,0,0,0.10);
  transition: background 250ms ease-out;
}

.nav {
  &-about {
    background: $nav-about;
  }

  &-experience {
    background: $nav-experience;
  }

  &-portfolio {
    background: $nav-portfolio;
  }

  &-contact {
    background: $nav-contact;
  }

  &-underlay {
    height:36px;
  }

  &-link {
    margin:0 20px 0 20px;
    font-weight:400;
    color: $white;
    text-decoration: none;

    &-active {
      margin:0 20px 0 20px;
      font-weight:400;
      text-decoration: none;
      color: $camel;
      cursor: default;

        &:hover {
          color: $camel;
        }
    }

    &:hover {
      color: $camel;
    }
  }

  @media only screen and (min-width: $responsive-width) {
    &-link, &-link-active {
      font-size: 16px;
    }
  }

}



